<template>
  <div class="inputContainer">
    <div class="formContainer">
      <FormulateForm
      v-model="formValues"
      :schema="[getInputFields({main: configName, sub: mainRoute, subsub: subRoute})]"
      :invalid-message="showValidationFields"
      @change="saveChange"
      @submit="goToNext"
      >
          <div class="input_subpage_nav">
              <router-link :to="getPath('previous')">
              <FormulateInput type="button" :label="$t('message.dataupload.label.preview')" :disabled="true ? routesArray.indexOf(this.$route.path) === 0 : false"/>
              </router-link>
              <FormulateInput type="submit" :label="$t('message.dataupload.label.next')"/>

          </div>

      </FormulateForm> 
    </div>
    <div class="infoContainer">
      <h5><i class="material-icons">info</i>{{$t('message.dataupload.info.information')}}</h5>
        <div id="info">{{$t('message.dataupload.info.defaultdescription')}}</div>      
    </div>
  </div>
         

</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import {getPath} from '../utils/helpers/routing_helpers'
import {saveChange, showValidationFields, goToNext} from '../utils/helpers/submit_helpers'

export default {
    data(){
      return{
        formValues: {}
      }
    },
    props: {
        configName: String,
        mainRoute: String,
        subRoute: String,
        routesArray: Array
    },
    methods: {
      ...mapActions(['getValues']),
      getPath: getPath,
      saveChange: saveChange,
      showValidationFields: showValidationFields,
      goToNext: goToNext
    },
    computed: mapGetters(['getInputFields']),
    created(){
      // triggers fetching of all values belonging to an input with a source-property
      this.getValues(this.configName);
    },
    mounted(){

      // triggers reading values from localStorage on reload (auto fill in of values into input form)
      var valueName = 'inputValues_' + this.configName + '_' + this.mainRoute;
      if(this.subRoute){
        valueName = valueName + "_" + this.subRoute;
      }

      if(localStorage.getItem(valueName)){
        this.formValues = JSON.parse(localStorage.getItem(valueName));
      }
    },
    watch:{
        // triggers reading values from localStorage on route change withour reload (auto fill in of values into input form)
        $route(to, from){
          var valueName = 'inputValues_' + this.configName + '_' + this.mainRoute;
          if(this.subRoute){
            valueName = valueName + '_' + this.subRoute;
          }
          if(localStorage.getItem(valueName)){
            this.formValues = JSON.parse(localStorage.getItem(valueName));
          }
          to
          from
        }
    }
}
</script>

<style>

.inputContainer {
  display: flex;
}

.formContainer {
  width: 75%;
}

.infoContainer {
  width: 20%;
  padding: 10px;
  margin: 0 10px 0 10px;
  background-color: #ddd;
}

</style>