<template>
  <label :for="context.id">
    {{ context.label }}
    <a v-if="info" @click="displayInfo()" class="infoButton"><i class="material-icons">info</i></a>
  </label>
</template>

<script>
export default {
  props: {
    context: {
      type: Object,
      required: true
    },
    info: {
      type: [String, Boolean],
      default: false
    }
  },
  methods: {
    displayInfo(){
      document.getElementById('info').innerHTML = '<p>' + this.info + '</p>';
    }
  }
}
</script>

<style>
.infoButton{
  border-radius: 5px;
  cursor: pointer;
}

</style>
