<template>
    <div></div>
</template>

<script>
import {convertToInput} from '../utils/converter/convertToInput'

export default {
    props: ['id', 'profile'],
    methods: {
        convertToInput: convertToInput,
    },
    created(){
        this.convertToInput(this.profile, this.id);
        var firstRoute = this.$store.getters.getRoutes(this.profile)[0];
        this.$router.push({path: firstRoute});
    }
}
</script>