<template>
    <div>
      <div>
        <StepProgress :steps="getRouteNames(configName)" :current-step="routesArray.indexOf(this.$route.path)" :line-thickeness="5"/>
      </div>

      <div v-if="subRoute">
        <div v-if="subRoute === 'overview'">
          <OverviewPage :configName="configName" :mainRoute="mainRoute" :subRoute="subRoute" :routesArray="routesArray"/>
        </div>
        <div v-else>
          <DataInput :configName="configName" :mainRoute="mainRoute" :subRoute="subRoute" :routesArray="routesArray"/>
        </div>
          
      </div>
      <div v-else>
        <div v-if="mainRoute === 'overview'">
          <OverviewPage :configName="configName" :mainRoute="mainRoute" :subRoute="subRoute" :routesArray="routesArray"/>
        </div>
        <div v-else>
          <DataInput :configName="configName" :mainRoute="mainRoute" :subRoute="subRoute" :routesArray="routesArray"/>
        </div>
      </div>
            
    </div>
</template>

<script>
import OverviewPage from '../components/OverviewPage.vue'
import DataInput from '../components/DataInput.vue'
import {mapGetters} from 'vuex'
import StepProgress from 'vue-step-progress';
import 'vue-step-progress/dist/main.css';

export default {
    name: "Input",
    components: {
      OverviewPage,
      DataInput,
      StepProgress
    },
    props: [
        'mainRoute',
        'configName',
        'subRoute'
    ],
    computed: mapGetters(['getRoutes', 'getRouteNames']),
    created(){
      // fetches an ordered array of all available routes 
      this.routesArray = this.getRoutes(this.configName)
    }
}
</script>
<style>
.step-progress__step span{
  font-size: 30px;
  font-weight: bold;
}
.step-progress__step::after{
  height: 40px;
  width: 40px;
}
</style>
