<template>
  <div
    :class="`formulate-input-element formulate-input-element--${context.type}`"
    :data-type="context.type"
    v-on="$listeners"

  >
    <FormulateSlot
      name="prefix"
      :context="context"
    >
      <component
        :is="context.slotComponents.prefix"
        v-if="context.slotComponents.prefix"
        :context="context"
      />
    </FormulateSlot>

    <v-select
      v-model="context.model"
      :type="context.type"
      v-bind="context.attributes"
      @blur="context.blurHandler"
      @input="context.rootEmit('change')"
      :options="options"
      :reduce="label => label.code" 
      label="label"
    ></v-select>

    <FormulateSlot
      name="suffix"
      :context="context"
    >
      <component
        :is="context.slotComponents.suffix"
        v-if="context.slotComponents.suffix"
        :context="context"
      />
    </FormulateSlot>
  </div>
</template>

<script>
export default {
  props: {
    context: {
      type: Object,
      required: true
    }
  },
  computed: {
    model() {
      return this.context.model
    },
    options () {
      var valueArray = [];
      for(var index in this.context.options){
        var option = {
          label: this.context.options[index].label,
          code: this.context.options[index].value
        };
        valueArray.push(option);
      }

      return valueArray
    }
  }
}
</script>

