<template>
  <div>
    <div id="clearStorage">
      <FormulateInput type="button" :label="$t('message.dataupload.label.clear')" @click="clearStorage"/>
    </div>

    <router-view :name="configurationName"></router-view>
      
  </div>

</template>

<script>
import { mapGetters } from 'vuex';
export default {
  props: {
    configurationName: String
  },
  methods: {
    clearStorage(){
      localStorage.clear();

      // refresh page
      this.$router.go(); 
    }
  },
  computed: mapGetters(['getRoutes', 'getBaseUrl']),
  mounted(){
    var currentPath = this.$route.path;
    if(currentPath.endsWith(this.getBaseUrl + this.configurationName)){
      var firstRoute = this.getRoutes(this.configurationName)[0];
      this.$router.push({path: firstRoute});
    }
  }
}
</script>
<style>
  #clearStorage {
    display:flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 20px;
  }
</style>